/**
 * TelegramProvider - компонент для управления интеграцией с Telegram Mini App
 */

import React, { createContext, useContext, useEffect, useState, useCallback } from 'react';
import type { TelegramWebApp, TelegramUser } from '../types/telegram';
import { useGameStore } from '../store/gameStore';

interface TelegramContextType {
  webApp: TelegramWebApp | null;
  user: TelegramUser | null;
  isInTelegram: boolean;
  platform: string;
  colorScheme: 'light' | 'dark';
  token: string | null;
  isAuthenticating: boolean;
  isAuthenticated: boolean;
  authError: string | null;
  authenticate: () => Promise<void>;
  logout: () => void;
}

const TelegramContext = createContext<TelegramContextType>({
  webApp: null,
  user: null,
  isInTelegram: false,
  platform: 'unknown',
  colorScheme: 'dark',
  token: null,
  isAuthenticating: false,
  isAuthenticated: false,
  authError: null,
  authenticate: async () => {},
  logout: () => {}
});

export const useTelegram = () => useContext(TelegramContext);

interface TelegramProviderProps {
  children: React.ReactNode;
}

export const TelegramProvider: React.FC<TelegramProviderProps> = ({ children }) => {
  const [webApp, setWebApp] = useState<TelegramWebApp | null>(null);
  const [user, setUser] = useState<TelegramUser | null>(null);
  const [isInTelegram, setIsInTelegram] = useState(false);
  const [platform, setPlatform] = useState('unknown');
  const [colorScheme, setColorScheme] = useState<'light' | 'dark'>('dark');
  const [token, setToken] = useState<string | null>(() => (typeof sessionStorage !== 'undefined' ? sessionStorage.getItem('tg_token') : null));
  const [isAuthenticating, setIsAuthenticating] = useState(false);
  const [authError, setAuthError] = useState<string | null>(null);

  // Helper to push telegram user fields into game store (light touch – avoids creating new action)
  const syncPlayerToStore = useCallback((tgUser: TelegramUser) => {
    try {
      useGameStore.setState(prev => ({
        player: {
          ...prev.player,
          telegramId: tgUser.id.toString(),
          // Preserve other player fields; attach a displayName for UI if structure lacks names
          displayName: tgUser.username || tgUser.first_name || 'Player'
        }
      }));
    } catch (e) {
      console.warn('Failed to sync telegram user to game store', e);
    }
  }, []);

  const performAuth = useCallback(async (tgWebApp: TelegramWebApp) => {
    if (!tgWebApp?.initDataUnsafe?.user) return;
    if (token) return; // already have
    setIsAuthenticating(true);
    setAuthError(null);
    try {
      const backend = (import.meta as any).env?.VITE_BACKEND_URL || 'http://localhost:3001';
      const body = {
        user: tgWebApp.initDataUnsafe.user,
        auth_date: tgWebApp.initDataUnsafe.auth_date,
        hash: tgWebApp.initDataUnsafe.hash
      };
      const res = await fetch(`${backend}/auth/telegram`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(body)
      });
      if (!res.ok) throw new Error(`Auth failed: ${res.status}`);
      const json = await res.json();
      if (json.token) {
        setToken(json.token);
        if (typeof sessionStorage !== 'undefined') sessionStorage.setItem('tg_token', json.token);
        syncPlayerToStore(tgWebApp.initDataUnsafe.user!);
      }
    } catch (e:any) {
      console.error('Telegram auth error', e);
      setAuthError(e.message || 'Auth error');
    } finally {
      setIsAuthenticating(false);
    }
  }, [token, syncPlayerToStore]);

  const logout = useCallback(() => {
    setToken(null);
    if (typeof sessionStorage !== 'undefined') sessionStorage.removeItem('tg_token');
  }, []);

  const authenticate = useCallback(async () => {
    if (webApp) await performAuth(webApp);
  }, [webApp, performAuth]);

  useEffect(() => {
    // Real Telegram environment
    if (typeof window !== 'undefined' && window.Telegram?.WebApp) {
      const tgWebApp = window.Telegram.WebApp as TelegramWebApp;
      tgWebApp.ready();
      tgWebApp.expand();
      setWebApp(tgWebApp);
      setIsInTelegram(true);
      setPlatform(tgWebApp.platform || 'unknown');
      setColorScheme(tgWebApp.colorScheme || 'dark');
      if (tgWebApp.initDataUnsafe?.user) {
        setUser(tgWebApp.initDataUnsafe.user);
        syncPlayerToStore(tgWebApp.initDataUnsafe.user);
        performAuth(tgWebApp); // fire & forget
      }
      tgWebApp.setHeaderColor('#000000');
      tgWebApp.setBackgroundColor('#000000');
      tgWebApp.disableClosingConfirmation();
      console.log('[Telegram] Initialized', {
        platform: tgWebApp.platform,
        colorScheme: tgWebApp.colorScheme,
        user: tgWebApp.initDataUnsafe?.user,
        version: tgWebApp.version
      });
      return;
    }
    // Debug / local non-Telegram mode with mock user (enable with ?tg_debug=1)
    if (typeof window !== 'undefined') {
      const params = new URLSearchParams(window.location.search);
      if (params.get('tg_debug') === '1') {
        const mockUser: TelegramUser = {
          id: 999999,
            first_name: 'Debug',
            last_name: 'User',
            username: 'debug_player'
          };
        setUser(mockUser);
        syncPlayerToStore(mockUser);
        console.log('[Telegram] Debug mock user active');
      }
    }
  }, [performAuth, syncPlayerToStore]);

  const contextValue: TelegramContextType = {
    webApp,
    user,
    isInTelegram,
    platform,
    colorScheme,
    token,
    isAuthenticating,
    isAuthenticated: !!token,
    authError,
    authenticate,
    logout
  };

  return (
    <TelegramContext.Provider value={contextValue}>
      {children}
    </TelegramContext.Provider>
  );
};

export default TelegramProvider;
