import React, { useRef, useState, useEffect } from 'react';
import { WebGPUManager } from '../webgpu/WebGPUManager';
import { ParticleSystem } from '../webgpu/ParticleSystem';
import { generateLightning, drawLightning } from '../webgpu/LightningEffect';
import { effectsBus } from '../effects/effectsBus';

interface WebGPUStatus { supported: boolean; message: string; adapterInfo?: { name?: string; features?: string[] }; reason?: string }

export const WebGPUCanvas: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const [status, setStatus] = useState<WebGPUStatus>({ supported: true, message: 'Initializing...' });
  const [fps, setFps] = useState(0);
  const framesRef = useRef(0);
  const lastFpsUpdateRef = useRef(performance.now());
  const lastTimeRef = useRef(performance.now());
  const particleRef = useRef<ParticleSystem | null>(null);
  const lightningRef = useRef<{ segs: any[]; t: number } | null>(null);
  // Detect Telegram Mini App environment early
  const isTelegramMiniApp = typeof window !== 'undefined' && (window as any).Telegram?.WebApp;
  const targetParticleMaxRef = useRef(isTelegramMiniApp ? 300 : 500);
  const adaptAccumulatorRef = useRef(0);
  // WebGPU pipeline refs
  const wgpuReadyRef = useRef(false);
  const deviceRef = useRef<any>(null);
  const contextRef = useRef<any>(null);
  const pipelineRef = useRef<any>(null);
  const formatRef = useRef<any>(null);

  const ensurePipeline = () => {
    if (!wgpuReadyRef.current || !deviceRef.current || !contextRef.current || pipelineRef.current) return;
    const device = deviceRef.current;
    const format = formatRef.current;
    const shader = device.createShaderModule({
      code: `struct VSOut { @builtin(position) pos: vec4f, @location(0) col: vec3f };
      @vertex fn vs(@builtin(vertex_index) i:u32) -> VSOut {
        var positions = array<vec2f,3>(vec2f(0.0,0.8), vec2f(-0.8,-0.8), vec2f(0.8,-0.8));
        var colors = array<vec3f,3>(vec3f(1,0.4,0.1), vec3f(0.1,0.6,1), vec3f(0.2,1,0.4));
        var out: VSOut; out.pos = vec4f(positions[i],0,1); out.col = colors[i]; return out; }
      @fragment fn fs(in:VSOut) -> @location(0) vec4f { return vec4f(in.col,1); }`
    });
    pipelineRef.current = device.createRenderPipeline({
      layout: 'auto',
      vertex: { module: shader, entryPoint: 'vs' },
      fragment: { module: shader, entryPoint: 'fs', targets: [{ format }] },
      primitive: { topology: 'triangle-list' }
    });
  };

  // Main animation / metrics loop (covers fallback rendering)
  useEffect(() => {
    let raf: number;
    const loop = () => {
      const now = performance.now();
      const dt = (now - lastTimeRef.current) / 1000;
      lastTimeRef.current = now;
      framesRef.current++;
      if (now - lastFpsUpdateRef.current > 500) {
        const delta = now - lastFpsUpdateRef.current;
        const recentFps = Math.round((framesRef.current * 1000) / delta);
        setFps(recentFps);
        // Adaptive particle budget: raise if fps>50, lower if <30
        adaptAccumulatorRef.current += delta;
        if (adaptAccumulatorRef.current > 2000) { // every ~2s
          adaptAccumulatorRef.current = 0;
          if (recentFps < 30) targetParticleMaxRef.current = Math.max(200, Math.floor(targetParticleMaxRef.current * 0.8));
          else if (recentFps > 55) targetParticleMaxRef.current = Math.min(2000, Math.floor(targetParticleMaxRef.current * 1.15));
          if (particleRef.current) particleRef.current.max = targetParticleMaxRef.current;
        }
        framesRef.current = 0;
        lastFpsUpdateRef.current = now;
      }
      // If WebGPU supported, render triangle each frame
      if (status.supported && wgpuReadyRef.current && deviceRef.current && contextRef.current) {
        ensurePipeline();
        if (pipelineRef.current) {
          try {
            const device = deviceRef.current;
            const context = contextRef.current;
            const encoder = device.createCommandEncoder();
            const view = context.getCurrentTexture().createView();
            const pass = encoder.beginRenderPass({
              colorAttachments: [{
                view,
                clearValue: { r: 0.04, g: 0.04, b: 0.07, a: 1 },
                loadOp: 'clear',
                storeOp: 'store'
              }]
            });
            pass.setPipeline(pipelineRef.current);
            pass.draw(3);
            pass.end();
            device.queue.submit([encoder.finish()]);
          } catch (e) {
            console.warn('WebGPU frame error', e);
          }
        }
      }
      // Fallback effects when WebGPU unsupported
      const canvas = canvasRef.current;
      if (canvas && !status.supported) {
        const ctx = canvas.getContext('2d');
        if (ctx) {
          if (!particleRef.current) particleRef.current = new ParticleSystem(targetParticleMaxRef.current);
          // spawn bursts
            if (Math.random() < 0.07) {
            const x = Math.random() * canvas.width;
            const y = Math.random() * canvas.height * 0.6;
            particleRef.current.spawnBurst(x, y, 20 + Math.random()*20);
            if (Math.random() < 0.35) {
              const lx = Math.random() * canvas.width;
              lightningRef.current = { segs: generateLightning(lx, 0, lx + (Math.random()-0.5)*140, canvas.height * (0.4+Math.random()*0.4)), t: 0 };
            }
          }
          particleRef.current.update(dt);
          ctx.fillStyle = '#090910';
          ctx.fillRect(0, 0, canvas.width, canvas.height);
          particleRef.current.draw2D(ctx);
          if (lightningRef.current) {
            lightningRef.current.t += dt * 3.2;
            const alpha = 1 - lightningRef.current.t;
            if (alpha > 0) drawLightning(ctx, lightningRef.current.segs, alpha); else lightningRef.current = null;
          }
        }
      }
      raf = requestAnimationFrame(loop);
    };
    raf = requestAnimationFrame(loop);
    return () => cancelAnimationFrame(raf);
  }, [status.supported]);

  // Init path (manager -> triangle fallback -> 2D fallback)
  useEffect(() => {
    if (!canvasRef.current) return;
    let mounted = true;
    const canvas = canvasRef.current;
    (async () => {
      const mgr = WebGPUManager.instance;
  const res = await mgr.init(canvas);
      if (res.ok) {
        if (mounted) setStatus({ supported: true, message: 'WebGPU ready', adapterInfo: { name: res.adapterName, features: res.features } });
  // store refs for render loop
  deviceRef.current = mgr.getDevice();
  contextRef.current = mgr.getContext();
  formatRef.current = mgr.getFormat();
  wgpuReadyRef.current = true;
        return;
      }
      // If manager failed, attempt inline minimal triangle (reuse old logic)
      const tri = await (async () => {
        if (!('gpu' in navigator)) return { supported: false, message: 'WebGPU not supported', reason: 'Navigator.gpu missing (browser build / flag disabled)' };
        try {
          const adapter = await (navigator as any).gpu.requestAdapter();
          if (!adapter) return { supported: false, message: 'No adapter', reason: 'requestAdapter() returned null (sandbox / driver / context restrictions)' };
          const device = await adapter.requestDevice();
          const ctx = canvas.getContext('webgpu') as any;
          if (!ctx) return { supported: false, message: 'No context', reason: 'getContext("webgpu") failed — experimental in this WebView' };
          const format = (navigator as any).gpu.getPreferredCanvasFormat();
          ctx.configure({ device, format, alphaMode: 'premultiplied' });
          const shader = device.createShaderModule({ code: `@vertex fn v(@builtin(vertex_index) i:u32)->@builtin(position) vec4f{var p=array<vec2f,3>(vec2f(0.0,0.6),vec2f(-0.6,-0.6),vec2f(0.6,-0.6));return vec4f(p[i],0,1);} @fragment fn f()->@location(0) vec4f{return vec4f(1,0.5,0.15,1);}` });
          const pipe = device.createRenderPipeline({ layout:'auto', vertex:{ module:shader, entryPoint:'v'}, fragment:{ module:shader, entryPoint:'f', targets:[{format}] }, primitive:{ topology:'triangle-list'} });
          const draw = () => {
            const enc = device.createCommandEncoder();
            const view = ctx.getCurrentTexture().createView();
            const pass = enc.beginRenderPass({ colorAttachments:[{ view, clearValue:{r:0.05,g:0.05,b:0.08,a:1}, loadOp:'clear', storeOp:'store'}]});
            pass.setPipeline(pipe); pass.draw(3); pass.end(); device.queue.submit([enc.finish()]);
            if (mounted && status.supported) requestAnimationFrame(draw); // Only continue if still marked supported
          };
          requestAnimationFrame(draw);
          return { supported: true, message: 'WebGPU (triangle)' } as WebGPUStatus;
  } catch (e:any) { return { supported:false, message:'Inline init failed', reason: e?.message }; }
      })();
      if (mounted) setStatus(tri);
      if (!tri.supported) {
        // final fallback is handled by animation loop which sees supported=false
        if (isTelegramMiniApp && tri.reason && !tri.reason.includes('Telegram')) {
          // Enrich reason for Mini App context to reduce confusion
          setStatus(prev => ({ ...prev, reason: prev.reason ? prev.reason + ' (Telegram WebView: WebGPU часто отключён)' : 'Telegram WebView: WebGPU отключён' }));
        }
      }
    })();

    const handleResize = () => {
      if (!canvasRef.current) return;
      const parent = canvasRef.current.parentElement;
      if (!parent) return;
      const width = parent.clientWidth;
      const height = Math.min(300, Math.round(width * 0.75));
      canvasRef.current.width = width * window.devicePixelRatio;
      canvasRef.current.height = height * window.devicePixelRatio;
      canvasRef.current.style.width = width + 'px';
      canvasRef.current.style.height = height + 'px';
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => { mounted = false; window.removeEventListener('resize', handleResize); };
  }, []);

  useEffect(() => {
    const unsubLevel = effectsBus.on('levelUp', ({ level }) => {
      const c = canvasRef.current; if (!c) return; if (!particleRef.current) particleRef.current = new ParticleSystem(targetParticleMaxRef.current);
      particleRef.current.spawnBurst(c.width*0.5, c.height*0.4, 80 + level*5);
    });
    const unsubShock = effectsBus.on('shock', ({ severity }) => {
      const c = canvasRef.current; if (!c) return; if (!particleRef.current) particleRef.current = new ParticleSystem(targetParticleMaxRef.current);
      const mult = severity==='critical'?2.2:severity==='severe'?1.6:severity==='moderate'?1.2:1;
      for (let i=0;i<5*mult;i++) particleRef.current.spawnBurst(Math.random()*c.width, Math.random()*c.height*0.5, 10*mult);
    });
    const unsubStage = effectsBus.on('stageAdvance', ({ stage }) => {
      const c = canvasRef.current; if (!c) return; if (!particleRef.current) particleRef.current = new ParticleSystem(targetParticleMaxRef.current);
      particleRef.current.spawnBurst(c.width*0.5, c.height*0.2, 120 + stage*15);
    });
    const unsubDrop = effectsBus.on('itemDrop', ({ level }) => {
      const c = canvasRef.current; if (!c) return; if (!particleRef.current) particleRef.current = new ParticleSystem(targetParticleMaxRef.current);
      particleRef.current.spawnBurst(c.width*(0.3+Math.random()*0.4), c.height*0.3, 60 + level*20);
    });
    const unsubAi = effectsBus.on('aiAttack', ({ voltage }) => {
      const c = canvasRef.current; if (!c) return; if (!particleRef.current) particleRef.current = new ParticleSystem(targetParticleMaxRef.current);
      particleRef.current.spawnBurst(c.width*0.5, c.height*0.1, 100 + Math.min(100, Math.floor(voltage/5)));
    });
    return () => { unsubLevel(); unsubShock(); unsubStage(); unsubDrop(); unsubAi(); };
  }, []);

  return (
    <div style={{ border: '1px solid #333', padding: 8, borderRadius: 8, background: '#111', color: '#eee', fontFamily: 'monospace' }}>
      <div style={{ marginBottom: 6, fontSize: 12, display: 'flex', gap: 12, flexWrap: 'wrap' }}>
        <span>WebGPU: {status.message}</span>
        <span>FPS: {fps}</span>
        {status.adapterInfo?.name && <span>GPU: {status.adapterInfo.name}</span>}
      </div>
      <canvas ref={canvasRef} width={400} height={300} style={{ width: '100%', display: 'block', borderRadius: 4 }} />
  {!status.supported && (
        <div style={{ color: '#f66', marginTop: 8, fontSize: 11, lineHeight:1.4 }}>
          <div><strong>Fallback Canvas2D mode.</strong></div>
          <div>Причина: {status.reason || 'Недоступно'}</div>
          <div style={{opacity:0.8}}>Чтобы включить WebGPU:
            <ul style={{margin:'4px 0 0 16px', padding:0}}>
              <li>Chrome / Edge 113+: chrome://flags → "Unsafe WebGPU" → Enabled</li>
              <li>Обновить видеодрайверы (если adapter null)</li>
              <li>Использовать обычный браузер вместо Telegram WebView</li>
              <li>HTTPS origin обязателен (localhost допустим)</li>
      {isTelegramMiniApp && <li>В Telegram Mini App WebGPU ещё не доступен на большинстве устройств</li>}
            </ul>
          </div>
        </div>
      )}
    </div>
  );
};

export default WebGPUCanvas;
