// Simple global effects event bus for decoupling game logic from rendering layer
// Provides typed events (ADD ONLY LIGHTWEIGHT PAYLOADS) consumed by visual layers like WebGPUCanvas or BabylonStage

export type EffectEventMap = {
  levelUp: { level: number; voltsReward: number };
  shock: { severity: 'mild' | 'moderate' | 'severe' | 'critical' };
  stageAdvance: { stage: number };
  itemDrop: { type: string; level: number };
  aiAttack: { voltage: number };
  outletClick: { time: number };
  duelState: { roomId: string; players: Record<string, { id: string; score: number; volts: number; streak: number }> };
};

export type EffectEventName = keyof EffectEventMap;

export type EffectEventListener<E extends EffectEventName> = (payload: EffectEventMap[E]) => void;

class EffectsBus {
  // Simpler runtime structure with loose typing internally; external API remains typed
  private listeners: Record<string, Set<(...args: any[]) => void>> = {};

  on<K extends EffectEventName>(event: K, listener: EffectEventListener<K>) {
    (this.listeners[event] ||= new Set()).add(listener as any);
    return () => this.off(event, listener);
  }

  off<K extends EffectEventName>(event: K, listener: EffectEventListener<K>) {
    this.listeners[event]?.delete(listener as any);
  }

  emit<K extends EffectEventName>(event: K, payload: EffectEventMap[K]) {
    this.listeners[event]?.forEach(l => {
      try { l(payload); } catch { /* ignore */ }
    });
  }
}

export const effectsBus = new EffectsBus();
